import React from 'react';

import Video from '../../components/video/video.component';
import { 
  FaPlus,
  FaTrashAlt } from 'react-icons/fa';

const data = {
  serverData: 'http://localhost:3000/',
  id: '',
  videoUrl: 'https://vimeo.com/308797532',
  videoDuration: '',
  videoProgress: '',
  isLogginIn: true,
  isPlaying: false,
  addTodo: '',
  addTime: '',
  todos: []
}

const VideoEditor = () => (
  <div className='videopage-grid'>
    <header className='header'>
      <div className='header__user'>Ahoj User</div>
      <div className='header__logout'>Odhlásiť</div>
    </header>
    <main className='video-section'>
      <div className='video-section__video'>
        <Video video={data.videoUrl}/>
      </div>
      <div className='video-section__infopanel'>
        <h2 className='video-section__infopanel-heading'>
          Video Title
        </h2>
      </div>
    </main>
    <aside className='todo-list'>
      <div className='todo-list__input'>
        <input 
          className='todo-list__input-time' 
          type='text' 
          name='time' 
          placeholder='5:45'
        />
        <input 
          className='todo-list__input-todo' 
          type='text' 
          name='todo' 
          placeholder='Vaša pripomienka' 
        />
        <button 
          className='todo-list__input-submit'
          type='submit'
        >
          <FaPlus/>
        </button>
      </div>
      <div className='todo-list__todos'>
        <div className='todo-list__todos-todo'>
          <div className='todo-list__todos-todo-time'>
            5:45
          </div>
          <div className='todo-list__todos-todo-todo'>
            Testing
          </div>
          <div className='todo-list__todos-todo-delete'>
            <FaTrashAlt/>
          </div>
        </div>
        <div className='todo-list__todos-todo'>
          <div className='todo-list__todos-todo-time'>
            5:45 - 12:20
          </div>
          <div className='todo-list__todos-todo-todo'>
          Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor incididunt ut labore et dolore magna aliqua. Ut enim ad minim veniam, quis nostrud exercitation
          </div>
          <div className='todo-list__todos-todo-delete'>
            <FaTrashAlt/>
          </div>
        </div>
      </div>
    </aside>
  </div>
)

export default VideoEditor;